.main-nav-search-result {
  position: absolute;
  top: 15px;
  right: 0;
  z-index: 2;
  background-color: white;
  width: 400px;
  list-style-type: none;
  padding: 8px 3% 5px 3%;
  background-color: grey;
}

.main-nav-search-result-item-img {
  width: 40%;
  height: auto;
}

.main-nav-search-result p {
  display: inline-block;
  width: 57%;
  margin: 0 0 1% 2%;
  height: 100%;
  font-size: 14px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.main-nav-search-result > li {
  width: 100%;
}

.main-nav-search-result > li:hover {
  background-color: rgb(34, 34, 66);
}

.main-nav-search-result > li > a {
  display: block;
  height: 100%;
  text-decoration: none;
  color: antiquewhite;
  padding-top: 5px;
  padding-left: 5px;
  border-top: 1px black solid;
}
