:root {
  --err-color: #c15755;
}

.login-form-important {
  text-transform: uppercase;
  font-weight: 500;
}

.login-form-important-blue {
  text-transform: uppercase;
  font-weight: 500;
  color: #1999ff;
  text-transform: uppercase;
}

#login-form {
  display: flex;
  flex-wrap: nowrap;
  background-color: #181a21;
  padding: 2rem;
}

#login-form > fieldset:disabled input,
#login-form > fieldset:disabled label {
  cursor: not-allowed;
}

#login-form-left {
  display: flex;
  flex-direction: column;
  gap: 0.5em;
  min-width: 70%;
  width: 100%;
}

#login-form input[type="text"],
#login-form input[type="password"] {
  color: inherit;
  background-color: #32353c;
  border-radius: 2px;
  font-size: 15px;
  padding: 10px;
  width: 95%;
  transition: outline-color 400ms ease-in-out;
}

#login-form input:hover,
label[for="remember"] {
  filter: brightness(1.05);
}

label[for="remember"] {
  cursor: pointer;
  vertical-align: middle;
}

#remember {
  display: grid;
  place-content: center;
  -webkit-appearance: none;
  appearance: none;
  margin: 0;
  font: inherit;
  height: 1em;
  width: 1em;
  background-color: #32353c;
}

#remember:focus {
  outline: 1px solid #06bfff;
}

#remember:before {
  content: "";
  width: 0.75em;
  height: 0.75em;
  background-color: white;
  clip-path: polygon(14% 44%, 0 65%, 50% 100%, 100% 16%, 80% 0%, 43% 62%);
  transform: scale(0);
  transition: 100ms transform ease-in-out;
}

#remember:checked:before {
  transform: scale(1);
}

#remember-container {
  display: flex;
  position: relative;
  align-items: center;
  gap: 0.5em;
}

#remember-tool-tip {
  position: absolute;
  opacity: 0;
  visibility: hidden;
  background-color: #32353c;
  padding: 0.5em;
  border-radius: 3px;
  transition: opacity 500ms ease-in-out;
}

#remember:hover ~ #remember-tool-tip,
#remember:focus ~ #remember-tool-tip {
  visibility: visible;
  opacity: 1;
  z-index: 2;
  left: 20%;
  top: 10%;
}

#login-form button[type="submit"] {
  background: linear-gradient(90deg, #06bfff 0%, #2d73ff 100%);
  border-radius: 2px;
  color: #fff;
  font-size: 18px;
  padding: 0.66em;
  font-weight: 500;
  font-family: inherit;
  text-align: center;
  letter-spacing: 0.03em;
  width: 60%;
  margin: auto;
}

#login-form button[type="submit"]:not(:disabled):hover {
  filter: brightness(1.05);
}

.submit-loading {
  padding: 0.1em !important;
}

#login-form-help {
  margin: auto;
}

#login-form a {
  text-decoration: underline;
}

#login-form a:hover {
  filter: brightness(1.05);
}

@media screen and (max-width: 700px) {
  #login-form {
    flex-direction: column;
    align-items: center;
    background-color: transparent;
    gap: 2.5em;
  }
  #login-form button[type="submit"] {
    width: 95%;
    margin: 0;
  }
}

.invalid {
  outline: 1px var(--err-color) solid;
}

.login-form-err-msg {
  visibility: hidden;
  margin: 0;
  opacity: 0;
  font-size: 12px;
  font-weight: 500;
  transition: opacity 300ms ease-in-out, color 300ms ease-in-out;
}

.login-form-err-msg-visible {
  visibility: visible;
  margin: 0 auto;
  opacity: 1;
  color: var(--err-color);
}
