header {
  background: var(--bg-black-primary);
  color: #b8b6b4;
  font-size: 14px;
}

@media screen and (max-width: 1020px) {
  header {
    position: fixed;
    z-index: 10;
    width: 100%;
  }

  header + * {
    padding-top: var(--header-content-sm-height);
  }
}

#header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  width: var(--header-content-width);
  height: var(--header-content-height);
  margin: 0 auto;
  padding: 0.6em 0;
}

#header-loader-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: var(--header-content-height);
}

#header-loader-container > .lds-ring {
  height: calc(var(--header-content-height) / 1.6);
  width: calc(var(--header-content-height) / 1.6);
}

.user-avatar-img-sm {
  width: 34px;
  height: 34px;
}
