#lang-modal-container {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  min-width: var(--main-min);
  background-color: rgba(0, 0, 0, 0.4);
  z-index: 5;
  overflow: auto;
}

#lang-modal {
  padding: 0.8rem;
  position: absolute;
  width: 90%;
  min-width: var(--main-min);
  background: radial-gradient(
      circle at top left,
      rgba(74, 81, 92, 0.4) 0%,
      rgba(75, 81, 92, 0) 60%
    ),
    #25282e;
  text-align: right;
}

#lang-modal > *:not(:last-child) {
  margin-bottom: 0.8em;
}

#lang-modal:before {
  content: "";
  width: 100%;
  height: 1px;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: inherit;
  background: linear-gradient(to right, #00ccff, #3366ff);
}

#lang-modal > h2 {
  text-align: left;
  margin-top: 0.2rem;
}

#lang-modal svg {
  display: block;
  height: 25px;
  margin: auto;
}

#lang-modal svg:hover {
  fill: white;
}

#lang-select {
  font-size: 24px;
  width: 100%;
}

.close-btn {
  height: auto;
  padding: 0;
  position: absolute;
  top: 8px;
  right: 2px;
}
