#login-main {
  background: radial-gradient(rgba(24, 26, 33, 0) 0%, #181a21 100%) fixed
      no-repeat,
    url(https://store.cloudflare.steamstatic.com/public/shared/images/joinsteam/new_login_bg_strong_mask.jpg)
      center top no-repeat,
    #181a21;
  width: 100%;
  min-height: 100vh;
}

#login-container {
  color: #afafaf;
  font-size: 12px;
  width: 700px;
  margin: auto;
}

#login-container > h1 {
  text-transform: uppercase;
  color: #fff;
  font-size: 32px;
  letter-spacing: 0.055em;
  font-weight: 200;
  padding-top: 5rem;
  padding-bottom: 3rem;
  text-shadow: 1px 1px 1px white;
}

@media screen and (max-width: 700px) {
  #login-main {
    background: radial-gradient(rgba(24, 26, 33, 0) 0%, #181a21 100%) fixed
        no-repeat,
      url(https://store.akamai.steamstatic.com/public/shared/images/joinsteam/new_login_bg_strong_mask_mobile.jpg);
  }

  #login-container {
    width: 340px;
  }
  #login-container > h1 {
    text-align: center;
    padding-top: 1rem;
  }
}
