#main-nav-container {
  --main-nav-min-height: 290px;
  width: var(--header-content-width);
  margin: 0 auto;
  text-align: right;
  font-size: var(--font-size-default);
  max-width: 100%;
}

#main-nav {
  display: flex;
  justify-content: space-between;
  padding-right: 2px;
  background: linear-gradient(
    90deg,
    rgba(62, 103, 150, 0.919) 11.38%,
    rgba(58, 120, 177, 0.8) 25.23%,
    rgb(15, 33, 110) 100%
  );
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.4);
  width: 100%;
}

@media screen and (max-width: 1020px) {
  #main-nav-container {
    width: 100%;
    text-align: left;
  }

  #main-nav {
    flex-direction: column;
  }

  #main-nav-loader .lds-ring {
    height: 5rem;
    width: 5rem;
  }
}

#main-nav-loader {
  display: flex;
  justify-content: center;
  align-items: center;
  height: var(--main-nav-min-height);
}
