#side-menu-user-infos {
  color: #57cbde;
  box-shadow: inset 0 -4px 8px 0 #000000;
  background: linear-gradient(107deg, #24313f 0%, #171a21 33%);
  margin: 0;
  padding: 0.8em;
  max-width: 100%;
  white-space: nowrap;
}

.user-avatar {
  vertical-align: middle;
  padding: 2px;
  background: linear-gradient(to bottom, #41778f 5%, #3d697b 95%);
  margin-right: 0.6em;
}

#side-menu-user-infos-main {
  margin: 0.5em 0;
}

#side-menu-user-infos a:not(#side-menu-user-infos-main) {
  font-size: 0.75em;
}
