#account-menu {
  background-color: var(--bg-black-primary);
  box-shadow: var(--box-shadow-default);
  font-size: var(--font-size-default);
  right: 0;
  top: 125%;
  padding: 0.5em 0;
  line-height: 2.25em;
  white-space: nowrap;
  max-width: 320px;
}
#account-menu a {
  padding: 0 1.5em;
}

#account-menu a:hover,
#account-menu a:focus {
  filter: brightness(1.2);
}

#account-drop-down-persona {
  display: inline-block;
  text-overflow: ellipsis;
  max-width: 100%;
  overflow-x: hidden;
}

#account-drop-down {
  white-space: nowrap;
  max-width: 100%;
}
