button {
  font: unset;
  color: unset;
  background-color: unset;
  border: unset;
  cursor: pointer;
  transition: filter 300ms ease-in-out;
}

button:disabled {
  cursor: not-allowed;
  filter: opacity(0.75) brightness(0.75) grayscale(1);
}

svg {
  fill: currentColor;
  height: 1.5em;
}

input {
  border: none;
  outline: none;
}

input[type="checkbox"] {
  cursor: pointer;
}

fieldset {
  border: 0;
  padding: 0.01em 0 0 0;
  margin: 0;
}

label {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  user-select: none;
}

h1 {
  margin: 0;
}
