details > summary {
  cursor: pointer;
  list-style: none;
}

.accordion-item-wrapper {
  display: grid;
  grid-template-rows: 0fr;
  transition: grid-template-rows 500ms ease-in-out;
}

.accordion-item-wrapper > div {
  overflow: hidden;
}

.unroll {
  grid-template-rows: 1fr;
}

.rotate180 {
  transform: rotate(180deg);
}

.accordion-summary {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
