#main-nav-categories-menu {
  width: var(--header-content-width);
  position: absolute;
  top: 100%;
  z-index: 10;
  white-space: normal;
  font-size: small;
  background: linear-gradient(
      90deg,
      rgba(33, 162, 255, 0.1) 1.89%,
      rgba(50, 50, 51, 0) 50%
    ),
    linear-gradient(180deg, #575860 11.6%, #3a4852 54.73%, #2c2d34 100%);
  text-align: left;
  padding: 0.5em 1.2em;
}

.nav-categories-title {
  color: #2ebfff;
  text-shadow: 0px 2px 3px rgba(0, 0, 0, 0.3);
  text-transform: uppercase;
  font-weight: 500;
  margin: 0.8em 0;
}

.nav-categories-special-block {
  position: relative;
  line-height: 1.6em;
  background-color: inherit;
  box-shadow: none;
  padding: 0;
  margin-top: 0;
}

.nav-categories-ul {
  line-height: 1.6em;
  background-color: inherit;
  box-shadow: none;
  padding: 0;
  margin-top: 0;
}

.nav-categories-ul > li:not(:last-child),
.nav-categories-special-block > li:not(:last-child) {
  margin-bottom: 0.5em;
}

.nav-categories-special-block:not(:last-child)::after {
  display: block;
  position: absolute;
  content: "";
  margin-top: 0.5em;
  width: 50%;
  height: 1px;
  background: linear-gradient(90deg, rgba(216, 216, 216, 0.3), transparent);
}

.nav-categories-column {
  position: relative;
  display: inline-block;
  vertical-align: text-top;
}

.nav-categories-column:not(:last-child)::after {
  display: block;
  position: absolute;
  content: "";
  height: 100%;
  width: 1px;
  top: 0;
  right: 1em;
  background: linear-gradient(
    95deg,
    rgba(0, 0, 0, 0.69) 0%,
    #4b4b4b70 52%,
    rgba(125, 125, 125, 0.1) 100%
  );
}

#nav-categories-genres {
  width: 60%;
  max-width: 60%;
  display: inline-block;
  vertical-align: text-top;
}

#nav-categories-genres-content {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  row-gap: 1em;
  max-width: 100%;
}

#nav-categories-genres-content div {
  display: flex;
  flex-wrap: wrap;
}
