#header-sm {
  display: flex;
  align-items: center;
  height: 62px;
  text-align: left;
}

#header-sm > div {
  height: 100%;
}

#header-sm > div > button {
  height: 65%;
}

#header-hamburger {
  height: 100%;
}

#header-sm > a {
  display: inline-block;
  height: 60%;
  margin: auto;
}

#header-sm > a > img {
  height: 100%;
}
