#wish-list-btn {
  display: inline-block;
  background-image: url("https://store.cloudflare.steamstatic.com/public/images/v6/storemenu/background_wishlist.jpg");
  text-transform: uppercase;
  font-size: 0.8em;
  padding: 0.25em 2em;
  border-radius: 0.1em;
  margin: 0.5em auto;
  transition: all 300ms ease-in-out;
}

#wish-list-btn:hover {
  background: linear-gradient(135deg, #ffffff 0%, #919aa3 100%);
  color: #111111;
  filter: brightness(1);
}
