.btn-green {
  border-radius: 2px;
  background: linear-gradient(to right, #75b022 5%, #588a1b 95%);
}

.btn-dark {
  border-radius: 4px;
  background: linear-gradient(to right, #32363f 5%, #32363f 95%);
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
  transition: all 0.2s ease-out;
  padding: 0.3em 0.5em;
}

.btn-dark:hover {
  filter: brightness(1.12);
}
