.home-nav-sec-link > [role="img"] {
  height: 16px;
  width: 16px;
  margin-right: 5px;
}

.home-nav-sec-link {
  display: block;
  font-size: 13px;
  text-decoration: none;
  color: #66c0f4;
  width: -moz-fit-content;
  width: fit-content;
}
.home-nav-sec-link:hover {
  color: white;
}

.home-nav-sec-link > * {
  vertical-align: middle;
  display: inline-block;
}
