#main-nav-menu-bar {
  position: relative;
}

#main-nav-menu-bar > *:not(.main-nav-cat) {
  display: inline-block;
  color: #e5e5e5;
  font-size: 13px;
  line-height: 34px;
  font-weight: bold;
  text-shadow: 0px 2px 3px rgb(0 0 0 / 30%);
  height: 100%;
}

#main-nav-menu-bar > *:hover {
  background-color: rgba(255, 255, 255, 0.151);
}

.main-nav-item {
  padding: 0 1em;
  cursor: pointer;
}

.main-nav-menu-bar-sm {
  display: flex;
  flex-direction: column;
}

@media screen and (max-width: 1020px) {
  .main-nav-menu {
    padding: 0;
    box-shadow: none;
  }
}

.main-nav-menu {
  background: linear-gradient(
      90deg,
      rgba(33, 162, 255, 0.1) 1.89%,
      rgba(50, 50, 51, 0) 50%
    ),
    linear-gradient(180deg, #575860 11.6%, #3a4852 54.73%, #2c2d34 100%);
  text-align: left;
  white-space: nowrap;
  padding-left: 1.8em;
  font-weight: normal;
}

.main-nav-menu > li {
  margin-bottom: 0.25em;
}

.main-nav-menu a {
  padding: 0.25em;
  border-bottom: transparent 2px solid;
  transition: border 300ms ease-in-out;
}

.main-nav-menu a:hover {
  border-bottom: white 2px solid;
}
