#header-action-bar {
  --download-btn-top-padding: 0.25em;
  --lang-menu-width: 20em;
  font-size: 12px;
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
  gap: 0.8em;
  width: 45%;
  height: 100%;
}

#header-download-btn {
  display: inline-flex;
  gap: 0.5em;
  background-color: #5c7e10;
  color: #e5e4dc;
  padding: var(--download-btn-top-padding) 0.7em;
  white-space: nowrap;
  text-overflow: ellipsis;
}

#header-download-btn > img {
  margin: auto;
  height: 1em;
}

#header-action-bar-lang-menu {
  background-color: var(--bg-black-primary);
  box-shadow: var(--box-shadow-default);
  width: var(--lang-menu-width);
  padding: 1em;
  line-height: 2em;
  font-size: 13px;
}

#header-action-bar-lang-menu button {
  text-align: left;
  width: 100%;
}

#header-action-bar-lang-menu button:not(:disabled):hover,
#header-action-bar-lang-menu button:not(:disabled):focus {
  filter: brightness(1.2);
}

#header-action-language img {
  display: inline-block;
  vertical-align: middle;
}

#header-action-bar-account-box {
  max-width: 40%;
  margin-right: 0.5em;
}

#header-action-bar-account-box > p {
  padding-top: 0.25em;
  padding-left: 6px;
  margin: 0;
}

.text-separator {
  border-left: 1px #b8b6b4 solid;
  height: 0.95em;
}

.action-bar-row {
  padding: var(--download-btn-top-padding);
  display: flex;
  justify-content: center;
  gap: 0.5em;
}

#header-action-language {
  padding: 0;
}

#header-avatar-box {
  background: linear-gradient(
    to bottom,
    rgba(106, 106, 106, 1) 5%,
    rgba(85, 85, 85, 1) 95%
  );
  padding: 2px;
}
#header-avatar-box > img {
  display: block;
  margin: auto;
}

.lang-opts-log-in-pos {
  left: calc(-1 * var(--lang-menu-width));
  top: 0;
}

.lang-opts-pos {
  right: 0;
  top: 1.5em;
}

#header-download-btn > img {
  height: 1.1em;
  width: 1.2em;
}
