#notifications-icon svg {
  fill: white;
  height: 1.25em;
}

#notifications-icon {
  position: relative;
  background-color: #262625;
  display: inline-flex;
  padding: 0.4em;
  border-radius: 100%;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: background-color 300ms ease-in-out;
}

#notifications-icon:hover,
#notifications-icon:focus {
  background-color: #575451;
}

#notifications-drop-down {
  position: absolute;
}

.notifications-menu-item {
  display: block;
}

.notifications-menu-item svg {
  vertical-align: middle;
  height: 1.25em;
}

.notifications-menu-item span {
  padding-left: 0.75em;
  vertical-align: middle;
}

.notifications-menu-item:hover,
.notifications-menu-item:focus {
  filter: brightness(1.2);
  background-color: var(--bg-black-primary-lighter);
}

#notifications-menu {
  font-size: var(--font-size-default);
  max-width: 320px;
  white-space: nowrap;
  right: 0;
  line-height: 2.5em;
  padding: 0.25em 0.5em;
  box-shadow: var(--box-shadow-default);
}

#notifications-menu > li:not(:last-child) > a {
  border-bottom: 1px var(--border-black-lighter) solid;
}

#notifications-menu a {
  text-overflow: ellipsis;
  height: 100%;
  padding-left: 0.5em;
  padding-right: 0.25em;
}
