#main-nav-search {
  position: relative;
  height: 90%;
  display: flex;
  align-items: center;
  margin: auto 0;
  padding: 2px;
  background-color: #316282;
  border-radius: 3px;
  background-color: rgba(96, 133, 155, 0.979);
  border: 1px solid rgba(0, 0, 0, 0.3);
}

#main-nav-search > input[type="search"] {
  height: 25px;
  border: none;
  outline: none;
  background-color: inherit;
  border-radius: inherit;
  text-indent: 0.8em;
  font-size: 14px;
  color: #c6d4df;
}

#main-nav-search > input[type="search"]::-webkit-search-cancel-button {
  display: none;
}

#main-nav-search > a > img {
  height: 25px;
  width: 25px;
  display: block;
}

#main-nav-search > [type="search"]::-webkit-input-placeholder {
  /* Edge */
  color: black;
  font-style: italic;
  opacity: 0.6;
}

#main-nav-search > [type="search"]:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: black;
  font-style: italic;
  opacity: 0.6;
}

#main-nav-search > [type="search"]::placeholder {
  color: black;
  font-style: italic;
  opacity: 0.6;
}

@media screen and (max-width: 1020px) {
  #main-nav-search {
    width: 96%;
    margin: 0.5em 2%;
  }
  #main-nav-search > input[type="search"] {
    width: 100%;
  }
}
