.home-trending-item {
  display: flex;
  align-items: center;
}

.home-trending-item-img-main {
  height: 353px;
  width: 616px;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  box-shadow: 1px 2px 5px black;
  z-index: 1;
}

.home-trending-item-img-secondary {
  display: inline-block;
  height: 69px;
  width: 162px;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  opacity: 0.6;
  margin: 5px 5px;
}

.home-trending-item-img-secondary-container {
  width: 360px;
  text-align: center;
  text-indent: 0;
}

.home-trending-item-product-infos {
  width: 360px;
  height: 353px;
  text-indent: 0.5em;
  background-image: url(https://store.akamai.steamstatic.com/public/images/v6/home/background_maincap_2.jpg);
  background-position: right;
  background-size: cover;
}
