.text-center {
  display: inline-block;
  text-align: center;
}

.no-m {
  margin: 0;
}

.text-cut {
  display: inline-block;
  max-width: 90%;
  text-overflow: ellipsis;
  overflow: hidden;
}

.d-block {
  display: block;
}

.m-auto {
  display: block;
}

.lowerCase {
  text-transform: lowercase;
}

.loader-container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 90vh;
}

.loader-container > .lds-ring {
  height: 100px;
  width: 100px;
}

.pos-unset {
  position: initial;
}

.text-left {
  text-align: left;
}

.width-20-percent {
  width: 20%;
}
