.header-nav-bar {
  white-space: nowrap;
  max-width: 60%;
}

.header-nav-bar > * {
  display: inline-block;
  padding: 0 0.5em;
  vertical-align: middle;
  text-transform: uppercase;
}

.header-nav-bar-link {
  display: inline-block;
  max-width: 100px;
  text-overflow: ellipsis;
  overflow: hidden;
  text-transform: uppercase;
  vertical-align: bottom;
}
