.carousel-arrow-btn {
  background: linear-gradient(
    90deg,
    rgba(98, 98, 98, 0.7147233893557423) 0%,
    rgba(117, 117, 117, 0.8939950980392157) 52%,
    rgba(91, 91, 91, 0.9416141456582633) 100%
  );
  opacity: 50%;
  min-height: 6rem;
  width: 2.5rem;
  padding: 0;
  border: none;
}

.carousel-arrow-btn > svg {
  margin: auto;
  fill: white;
  width: 100%;
}

.carousel-arrow-btn:hover {
  cursor: pointer;
  filter: saturate(2);
  opacity: 70%;
}
