.main-nav-sm-item {
  padding: 0.5em 1em;
  width: 100%;
}

.main-nav-sm-item:hover {
  background-color: rgba(255, 255, 255, 0.151);
}

.main-nav-detail {
  position: relative;
}

.main-nav-detail:not(:hover):after {
  content: "";
  position: absolute;
  width: 96%;
  margin-left: 0.5em;
  height: 1px;
  background: linear-gradient(90deg, rgba(216, 216, 216, 0.3), transparent);
}

.main-nav-detail ul {
  margin: 0;
  box-shadow: none;
}
