#header-side-menu {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #171a21;
  width: 280px;
  transition: left 0.5s, right 0.5s;
  position: absolute;
  top: 0;
  font-size: 22px;
  font-family: "Motiva Sans", Sans-serif;
  font-weight: 200;
  overflow-y: auto;
}

#header-side-menu > div > a {
  display: block;
  font-size: 0.75em;
  padding: 0.5em 0.5em;
  color: #8a8a8a;
}

.header-side-menu-item {
  display: block;
  font-size: 22px;
  border-top: 1px solid #2f3138;
  border-bottom: 1px solid #000000;
  padding: 0.5em 0.5em;
  height: 2.5em;
}

#header-side-menu summary {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.side-menu-details {
  transition: color 0.25s, background-color 0.25s;
}

.side-menu-details-open {
  background-color: #192533;
  color: #ffffff;
}

#header-side-menu details svg {
  transition: transform 0.25s;
}

.slide-in {
  left: 0 !important;
}

.left-out-280 {
  left: -280px;
}

#side-menu-dialog {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  z-index: 2;
  overflow: auto;
}

.side-menu-sub-menu {
  margin: 0;
  padding-left: 2em;
  font-size: 0.65em;
  border-top: 1px solid #2f3138;
  background-color: inherit;
}

.side-menu-sub-menu a {
  display: block;
  line-height: 1.5em;
}

.side-menu-sub-menu li:not(:first-child) {
  margin-top: 1em;
}

#header-side-menu-footer {
  font-size: 10px;
  white-space: normal;
  color: #8a8a8a;
  padding: 0 12px 24px 12px;
}
#header-side-menu-footer > p {
  margin: 0;
}

#header-side-menu-footer a:hover {
  color: white;
}

.text-limit {
  text-overflow: ellipsis;
  max-width: 20%;
}
